:root {
    --gap: 0.5rem;
    --gap-LG: 1.0;
    --gap-XL: 1.5rem;
    --muted: #777;
    --border: #ddd;
    --flag-low: #9CA3AF;
    --flag-med: #3B82F6;
    --flag-medhigh: #F59E0B;
    --flag-high: #EF4444;
    font-family: system-ui, -apple-system, Segoe UI, Roboto, sans-serif;
}



* { box-sizing: border-box; }

button {
    border: none;
    background: none;
    cursor: pointer;
    padding: 0.25rem;
    line-height: 1;
}

.hidden { display: none; }

/* Layout */
body {
    margin: 0;
    padding: 2rem;
    display: grid;
    grid:
    "header header" auto
    "projects todo-list" auto /
    minmax(100px,300px) minmax(100px,500px);
    gap: 2rem;
}

#heading {
    grid-area: header;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 1rem;
    margin: 0 0 2rem 0;
    width: 100%;
}

#title {
    grid-column: 1;
    justify-self: start;
    margin: 0;
    line-height: 1;
}

#undo-button {
    grid-column: 2;
    justify-self: end;
}

#projects-container {
    grid-area: projects;
    min-height: 100vh;
    background: teal;
    padding: var(--gap);
    color: white;
}

#projects-container i {
    color: white;
}

#todo-container     { grid-area: todo-list; }

/* Inputs */
#project-input {
    border: none;
    border-bottom: 1px solid white;
    background: none;
    color: white;
    margin-top: var(--gap-XL);
}

#project-input i {
    color: white;
}

#todo-list-input {
    border: none;
    border-bottom: 1px solid black;
    background: none;
    margin-top: var(--gap-XL);
}

/* Lists */
.list-container ul { list-style: none; margin: 0; padding: 0; }

.list-item {
    display: grid;
    align-items: center;
    grid:
    "priority task-name left right" auto
    "... task-notes ... ..." auto
    "... notes-input ... ..." auto
    "submit-notes-button cancel-notes-button ... ..." auto /
    auto 1fr auto auto;
    gap: var(--gap);
    padding-top: 1.25rem;
    border-bottom: 1px solid var(--border);
}

.item-entry.todo-item   { grid-area: task-name; margin: 0; }
.item-entry.project-item{ margin: 0; }

.row-left  { grid-area: left;  display: inline-flex; gap: .25rem; }
.row-right { grid-area: right; display: inline-flex; gap: .25rem; }

.delete-button, .rename-button, .add-notes, .check-off-button { color: #222; }
.delete-button:hover  { color: #ef4444; }
.rename-button:hover  { color: #2563eb; }
.add-notes:hover      { color: #2563eb; }
.check-off-button:hover { color: #16a34a; }

/* Notes */
.notes-container { grid-column: task-name; grid-area: task-notes; }
.task-notes { margin: 0; color: var(--muted); font-size: 0.875rem; }
.notes-input { grid-area: notes-input; width: 100%; min-height: 4rem; }
.submit-notes-button { grid-area: submit-notes-button; }
.cancel-notes-button { grid-area: cancel-notes-button; }

/* Completed */
.completed-task { opacity: .4; text-decoration: line-through; }

/* Active project */
.active-project { font-weight: 800; }

/* ---------- Priority Flags (custom dropdown) ---------- */

.priority-wrapper {
    grid-area: priority;
    position: relative;
    display: inline-block;
}

.priority-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 1.8rem;
    border: 1px solid var(--border);
    border-radius: 6px;
    background: #fff;
}

.priority-button i { font-size: 0.95rem; }

/* menu */
.priority-menu {
    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    margin: 0;
    padding: 0.25rem 0;
    list-style: none;
    background: #fff;
    border: 1px solid var(--border);
    border-radius: 6px;
    min-width: 8rem;
    box-shadow: 0 6px 24px rgba(0,0,0,.08);
    display: none;
    z-index: 10;
}

.priority-wrapper.open .priority-menu { display: block; }

.priority-option {
    display: flex;
    align-items: center;
    gap: .5rem;
    padding: 0.35rem 0.5rem;
    cursor: pointer;
    white-space: nowrap;
}

.priority-option:hover { background: #f7f7f7; }

.priority-label { font-size: 0.9rem; color: #333; }

/* Flag colors (Font Awesome .fa-flag) */
.flag-low      { color: var(--flag-low); }
.flag-med      { color: var(--flag-med); }
.flag-medhigh  { color: var(--flag-medhigh); }
.flag-high     { color: var(--flag-high); }

/* Make priority control compact on narrow screens */
@media (max-width: 520px) {
    .priority-menu { min-width: 7rem; }
}



